//"use client"
 
import { zodResolver } from "@hookform/resolvers/zod"
import { useForm } from "react-hook-form"
import { z } from "zod"

import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
    DialogClose
} from "./ui/dialog"

import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "./ui/form"

import { Button } from "./ui/button";
import { Input } from "./ui/input";

const FormSchema = z.object({
    discord: z.string().min(1, {
        message: "Заполните это поле",
    }),

    adress: z.string().min(1, {
        message: "Заполните это поле",
    }),

    cardnum: z.string().min(16, {
        message: "Номер карты должен состоять из 16 цифер",
    }),

    mmgg: z.string().min(1, {
        message: "Заполните это поле",
    }),

    cvc: z.string().min(1, {
        message: "Заполните это поле",
    }),

    ownerName: z.string().min(1, {
        message: "Заполните это поле",
    }),

    country: z.string().min(1, {
        message: "Заполните это поле",
    }),
})

const onSubmit = (data) => {
    console.log(JSON.stringify(data, null, 2));
}

const Pay = () => {
    const form = useForm({
        resolver: zodResolver(FormSchema),
        defaultValues: {
            discord: "",
            adress: "",
            cardnum: "",
            mmgg: "",
            cvc: "",
            ownerName: "",
            country: "",
            comment: "",
        },
    })

    return (

        <Dialog>
            <DialogTrigger><Button onClick={onclick} style={{ width: "305px" }}>Купить</Button></DialogTrigger>
                <DialogContent className={"payDialogContent lg:max-w-screen-lg overflow-y-scroll max-h-screen"}>
                    <DialogHeader>
                        <DialogTitle>Оплата</DialogTitle>
                        <DialogDescription>
                            Введите данные для оплаты
                        </DialogDescription>
                    </DialogHeader>

                    <Form {...form}>
                        <form onSubmit={form.handleSubmit(onSubmit)} className="w-2/3 space-y-6">
                            <FormField
                                control={form.control}
                                name="discord"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Ваш дискорд</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="adress"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Адрес доставки</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="cardnum"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Номер карты</FormLabel>
                                        <FormControl>
                                            <Input {...field} className={"payInput payInputCardnum"} />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="mmgg"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>ММ/ГГ</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="cvc"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>CVC/CVV2</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="ownerName"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>ФИО держателя карты</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="country"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Страна регистрации</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <FormField
                                control={form.control}
                                name="comment"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Комментарий к покупке</FormLabel>
                                        <FormControl>
                                            <Input {...field} className="payInput payInputComment" />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />

                            <Button type="submit" onClick={(event) => {
                                    const inputs = document.getElementsByClassName("payInput")
                                    let correct = 0

                                    for (let i = 0; i < inputs.length; i++) {
                                        const input = inputs[i]

                                        if (input.classList.contains("payInputCardnum")) {
                                            if (input.value.length >= 16) {
                                                correct++
                                            }
                                        } else if (input.classList.contains("payInputComment")) {
                                            correct++
                                        } else {
                                            if (input.value !== "") {
                                                correct++
                                            }
                                        }
                                    }

                                    console.log(correct);

                                    if (inputs.length === correct) {
                                        window.location = "/?t=1"
                                    }
                            }}>Оплатить</Button>
                        </form>
                    </Form>
            </DialogContent>
        </Dialog>
    )
}

export default Pay;