import * as React from "react"
import './App.css';

import {
    Alert,
    AlertDescription,
    AlertTitle
} from "./components/ui/alert";
import "./components/product";

import { HomeIcon } from "@radix-ui/react-icons";
import Product from "./components/product";

// Main
function App() {
    var urlParams = new URLSearchParams(window.location.search);
    let showBoughtToast = urlParams.get("t");

    if (showBoughtToast == 1) {
        return (
            <div className="App">
                <Alert className="topbar">
                    <HomeIcon color="white" />
                    <AlertTitle>SLT Shop</AlertTitle>
                    <AlertDescription>
                        Магазин SLT - лучший магазин на територии СВО (ZVO).
                    </AlertDescription>
                </Alert>

                <Alert className="thanks">
                    <AlertTitle>Спасибо за покупку!</AlertTitle>
                </Alert>

                <Product
                    name="Морской Ниггер"
                    price="83 рупия, 2 шекеля"
                    img="https://kartin.papik.pro/uploads/posts/2023-07/thumbs/1689410870_kartin-papik-pro-p-kartinki-na-plyazhe-negr-37.jpg"
                />
            </div>
        );
    } else {
        return (
            <div className="App">
                <Alert className="topbar">
                    <HomeIcon color="white" />
                    <AlertTitle>SLT Shop</AlertTitle>
                    <AlertDescription>
                        Магазин SLT - лучший магазин на територии СВО (ZVO).
                    </AlertDescription>
                </Alert>
    
                <Product
                    name="Морской Ниггер"
                    img="https://kartin.papik.pro/uploads/posts/2023-07/thumbs/1689410870_kartin-papik-pro-p-kartinki-na-plyazhe-negr-37.jpg"
                    price="83 рупия, 2 шекеля"
                />
            </div>
        );
    }
}

export default App;