import React from "react";

import {
    Card,
    CardContent,
    CardDescription,
    CardFooter,
    CardHeader,
    CardTitle,
} from "./ui/card";

import Pay from "./pay";

const Product = (props) => {
    return (
        <Card className="product">
            <CardHeader>
                <CardTitle>{props.name}</CardTitle>
                <CardDescription>
                    {props.price}
                </CardDescription>
            </CardHeader>
            <CardContent>
                <img
                    src={props.img}
                    style={{
                        width: "300px",
                        height: "150px",
                        borderRadius: "5px"
                    }}
                    alt="img"
                />
            </CardContent>
            <CardFooter>
                <Pay />
            </CardFooter>
        </Card>
    )
}

export default Product;